<template>
 <div>
    
      <div>表观密度试验</div>
      <el-button type="primary" @click="calculation">确定</el-button>
      <div  class="title">
          <div class="titleLeft">实验设备</div>
          <div class="titleRight">容量瓶（500ml）、电子天平、电热鼓风干燥箱</div>   
      </div> 
       <el-table
       :span-method="objectSpanMethod"
      
      :data="tableData"
      :border="true"
       size="small"
       :cell-style="cellStyle"
       :show-overflow-tooltip="true"
       :header-cell-style="{color:'black',borderColor:'black'}"
      style="width: 92%; margin-left: 10px;">
      <el-table-column
      align="center"
        prop="index"
        label="试验次数"
        width="180">
      </el-table-column>
      <el-table-column
        prop="sample"
        label="试样质量(m0)"
        width="180">
        <template slot-scope="scope">
      <el-input size="small" v-model="scope.row.sample" @change="handleEdit(scope.$index, scope.row)"></el-input>
    </template>

      </el-table-column>
      <el-table-column
        
        prop="sampleAndwaterAndbottle"
        label="试样、水及容量瓶总质量(m1)">
        <template slot-scope="scope">
      <el-input size="small" v-model="scope.row.sampleAndwaterAndbottle" @change="handleEdit(scope.$index, scope.row)"></el-input>
    </template>
      </el-table-column>
       <el-table-column
        prop="waterAndbottle"
        label="水及容量瓶总质量(m2)">
        <template slot-scope="scope">
      <el-input size="small" v-model="scope.row.waterAndbottle" @change="handleEdit(scope.$index, scope.row)"></el-input>
    </template>
      </el-table-column>
      <el-table-column
        prop="temperature"
        label="水温">
      </el-table-column>
      <el-table-column
        prop="temperatureData"
        label="水温修正系数">
        <template slot-scope="scope">
      <el-input size="small" v-model="scope.row.temperatureData" @change="handleEdit(scope.$index, scope.row)"></el-input>
    </template>
      </el-table-column>
      <el-table-column
        prop="density"
        label="表观密度">
      </el-table-column>
      <el-table-column
        prop="average"
        label="平均值">
      </el-table-column>
    </el-table>
 </div>
</template>

<script>

export default {
  name: 'Apparent',
  components:{
      
  },
  data () {
      
      return {
       tableData: [{
            index: '1',
            sample: '300',
            sampleAndwaterAndbottle: '840',
            waterAndbottle:'656',
            temperature:'5',
            temperatureData:'0.005',
            density:'',
            average:''



          },{
             index: '1',
            sample: '300',
            sampleAndwaterAndbottle: '862',
            waterAndbottle:'678',
            temperature:'5',
            temperatureData:'0.005',
            density:'',
            average:''
          }]
      }
  },
   methods: {
        objectSpanMethod({ row, column, rowIndex, columnIndex }) {
        if (columnIndex === 7) {
          if (rowIndex  === 8) {
            return {
              rowspan: 1,
              colspan: 8
            };
          } else {
            return {
              rowspan: 8,
              colspan: 8
            };
          }}},
    //设置单元格背景
            cellStyle({row, column, rowIndex, columnIndex}) {
                return 'height:35px!important; border-color:black!important; color:#000000!important; padding:0px!important; height:40px!important'
            },
            handleEdit(index, row) {
              console.log(index, row);
            },
            calculation(){
           
            for(var index in this.tableData){
              
              let J3 = Number(this.tableData[index].sample)
              let K3 = Number(this.tableData[index].sampleAndwaterAndbottle)
              let L3 = Number(this.tableData[index].waterAndbottle)
              let M3 = Number(this.tableData[index].temperatureData)
              this.tableData[index].density = Math.round((J3/(J3+L3-K3)-M3)*1000)
             
              
            }
            
                 this.tableData[0].average = (this.tableData[0].density+this.tableData[1].density)/2
            
             localStorage.setItem("P3",this.tableData[0].average);
              console.log(this.tableData)
            },

    }
    }
  

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title{
     display: flex;
 }
.titleLeft{

    border-top: 1px solid;
    border-left: 1px solid;
    width: 105px;
    font-size: 13px;
    text-align: center;
    margin-left: 10px;
        height: 40px;
    line-height: 40px;
   
}
.titleRight{

    border-top: 1px solid;
    border-left: 1px solid;
    border-right: 1px solid;
    width: 869px;
    font-size: 13px;
    text-align: center;
    height: 40px;
    line-height: 40px;
 
}
  .el-table td, .el-table th.is-leaf,.el-table--border, .el-table--group{
        border-color: black;
    }
    .el-table--border::after, .el-table--group::after, .el-table::before{
        background-color: black;
    }


</style>



