        <div class="titleLeft-1" style="display:flex">
                <el-input type='number' v-model="firstinput" style="width:23%;margin-left: 35%;" placeholder="请输入"></el-input>g</div>
            <div class="titleLeft-2" style="display:flex">
                <el-input type='number' v-model="secondiput" style="width:23%;margin-left:35%;"  placeholder="请输入"></el-input>g</div>
