            <screenTable  :edit='false'  :inputData="this.SecondInputAllData" title="累计筛余"></screenTable>     
            <screenTable  :edit='false'  :inputData="this.SecondInputAverageData" title="累计筛选平均值"></screenTable> 
      </div>
      </div>
