            index:7},
        ],
        inputData:[
           {data:'68',
            index:1},
            {data:'45',
            index:2},
            {data:'101',
            index:3},
            {data:'121',
            index:4},
            {data:'137',
            index:5},
            {data:'19',
            index:6},
             {data:'9',
            index:7},
        ],
        inputDataOne:[
            {data:'18',
