        ],
        unitData:[{
            unit:'公称颗粒'
        },
        {
            unit:'5mm'
        },
        {
            unit:'2.5mm'
        },
        {
            unit:'1.25mm'
        },
        {
            unit:'630µm'
        },
        {
            unit:'315µm'
        },
        {
            unit:'160µm'
        },
        {
            unit:'筛底'
        }
        ],
        averageAll:'',
        firstfineness:'',
        secondfineness:''
      }
  },
   methods: {
    calculation(){
        // B11 = firstinput 实验质量 (BX/500)*100   
        //this.inputData  
      
        var allInput  = 0
        var seconeAllInput = 0
        for(var index in this.inputData){
             
          allInput = allInput + Number(this.inputData[index].data)
          seconeAllInput = seconeAllInput + Number(this.inputDataOne[index].data)
        }
       
        if(allInput!=this.firstinput||seconeAllInput!=this.secondiput){
             this.$message({
                showClose: true,
                message: '输入数据错误！',
                type: 'error'
            });
        }else{
          //FirstInputData 第一次分计筛余
          //SeconDinputData   第二次分计筛余
          for(var index in this.inputData){
             var firstFJdata = 0
             firstFJdata =(Number(this.inputData[index].data)/this.firstinput)*100
             this.FirstInputData[index].data = this.formatPrice(firstFJdata)

            var secondFJdata = 0
            secondFJdata  =(Number(this.inputDataOne[index].data)/this.secondiput)*100
            this.SeconDinputData[index].data = this.formatPrice(secondFJdata)
          }
        }
        //FirstAllData  累计筛余
       // SecondInputAllData
         for(var index in this.FirstInputData){
         
           if(index == 0){
                this.FirstAllData[index].data =  this.FirstInputData[index].data
                this.SecondInputAllData[index].data =  this.SeconDinputData[index].data
               
           }else{
              
                this.FirstAllData[index].data =  this.formatPrice(Number(this.FirstAllData[index-1].data)+
                Number(this.FirstInputData[index].data))
                this.SecondInputAllData[index].data =  this.formatPrice(Number(this.SecondInputAllData[index-1].data)+
                Number(this.SeconDinputData[index].data))
           }

         }

         for(var index in this.FirstAllData){
         
            this.SecondInputAverageData[index].data = Math.round((Number(this.FirstAllData[index].data)+
                                                      Number(this.SecondInputAllData[index].data))/2)
         }
         //分类晒 平均值
         var  firstArr =  this.FirstAllData[0] // D3
         var  secondArr =  this.SecondInputAllData[0] //G3

       
         
         var firstall = 0 //C12
         for(var ind in  this.FirstAllData){
             if(ind!=0&&ind!=6){
                firstall=firstall+ Number(this.FirstAllData[ind].data) 
             }
           
         }
        var secondall = 0 // F12
         for(var inde in  this.SecondInputAllData){
               if(inde!=0&&inde!=6){
                secondall=secondall+ Number(this.SecondInputAllData[inde].data) 
             }
            
         }

        
        this.firstfineness =((firstall -5*(Number(this.FirstAllData[0].data)))/(100 - Number(this.FirstAllData[0].data))).toFixed(2) 
       
        this.secondfineness  = ((secondall -5*(Number(this.SecondInputAllData[0].data)))/(100 - Number(this.SecondInputAllData[0].data))).toFixed(2)
        
        this.averageAll =Math.round( (Number( this.firstfineness)+Number(this.secondfineness))/2)

       
    },
     formatPrice:function(value){
          let tempVal = parseFloat(value).toFixed(2)
          let realVal = tempVal.substring(0,  tempVal.length - 1)
                return realVal
            }
     ,

    inputDataAction(){
    //   console.log("输入数据 === ",this.inputData)
    },
    timeStyle(column) {

            return "font-size: 13px;text-align: center;border-bottom: 1px solid;height: 41px;padding: 1px;width:105px"
       }}
    }
  

</script>

