<template>
 <div>
    
      <div>堆积密度试验</div>
        <el-button type="primary" @click="calculation">确定</el-button>
      <div  class="title">
          <div class="titleLeft">实验设备</div>
          <div class="titleRight">电子计数秤、容量筒（1L）、漏斗、电热鼓风干燥箱</div>   
      </div> 
       <el-table
       :span-method="objectSpanMethod"
      
      :data="tableData"
      :border="true"
       size="small"
       :cell-style="cellStyle"
       :show-overflow-tooltip="true"
       :header-cell-style="{color:'black',borderColor:'black'}"
      style="width: 92%; margin-left: 10px;">
      <el-table-column
      align="center"
        prop="index"
        label="试验次数"
        width="180">
      </el-table-column>
      <el-table-column
        prop="sample"
        label="容量筒质量m1(kg)"
        width="180">
        <template slot-scope="scope">
      <el-input size="small" v-model="scope.row.sample" @change="handleEdit(scope.$index, scope.row)"></el-input>
    </template>

      </el-table-column>
      <el-table-column
        
        prop="sampleAndwaterAndbottle"
        label="容量筒和砂总质量m2(kg)">
        <template slot-scope="scope">
      <el-input size="small" v-model="scope.row.sampleAndwaterAndbottle" @change="handleEdit(scope.$index, scope.row)"></el-input>
    </template>
      </el-table-column>
       <el-table-column
        prop="waterAndbottle"
        label="堆积密度">
        <!-- <template slot-scope="scope">
      <el-input size="small" v-model="scope.row.waterAndbottle" @change="handleEdit(scope.$index, scope.row)"></el-input>
    </template> -->
      </el-table-column>
      <el-table-column
        prop="temperature"
        label="平均值">
      </el-table-column>
      <el-table-column
        prop="temperatureData"
        label="空隙率">

      </el-table-column>
      
    </el-table>
 </div>
</template>

<script>

export default {
  name: 'Accumulation',
  components:{
      
  },
  data () {
      
      return {
       tableData: [{
            index: '1',
            sample: '0.463',
            sampleAndwaterAndbottle: '1.932',
            waterAndbottle:'',
            temperature:'',
            temperatureData:'',
           


          },{
            index: '1',
            sample: '0.463',
            sampleAndwaterAndbottle: '1.935',
            waterAndbottle:'',
            temperature:'',
            temperatureData:''
           

          }]
      }
  },
   methods: {
        objectSpanMethod({ row, column, rowIndex, columnIndex }) {
        if (columnIndex === 5||columnIndex === 4) {
          if (rowIndex % 2 === 0) {
            return {
              rowspan: 2,
              colspan: 1
            };
          } else {
            return {
              rowspan: 0,
              colspan: 0
            };
          }}},
    //设置单元格背景
            cellStyle({row, column, rowIndex, columnIndex}) {
                return 'height:35px!important; border-color:black!important; color:#000000!important; padding:0px!important; height:40px!important'
            },
            handleEdit(index, row) {
              console.log(index, row);
            },
            calculation(){
              // (L7-K7)/J7*1000
              // (1-O7/P3)
            // sample: '0.463',
            // sampleAndwaterAndbottle: '1.932',
            // waterAndbottle:'',
          for(var index in this.tableData){
              
              let J7 = Number(this.tableData[index].index)
              let K7 = Number(this.tableData[index].sample)
              let L7 = Number(this.tableData[index].sampleAndwaterAndbottle)
           
              this.tableData[index].waterAndbottle = Math.round((L7-K7)/J7*1000)
            }

            for(var index in this.tableData){
             
              let P3 =localStorage.getItem("P3")

              this.tableData[index].temperature =Math.round( ( Number(this.tableData[0].waterAndbottle+this.tableData[1].waterAndbottle))/2)
              let O7 = Number(this.tableData[0].temperature)
              this.tableData[index].temperatureData =  (1-O7/P3).toFixed(2)
            }
            //  temperature:'',
            // temperatureData:'',

            }

    }
    }
  

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title{
     display: flex;
 }
.titleLeft{

    border-top: 1px solid;
    border-left: 1px solid;
    width: 105px;
    font-size: 13px;
    text-align: center;
    margin-left: 10px;
        height: 40px;
    line-height: 40px;
   
}
.titleRight{

    border-top: 1px solid;
    border-left: 1px solid;
    border-right: 1px solid;
    width: 869px;
    font-size: 13px;
    text-align: center;
    height: 40px;
    line-height: 40px;
 
}
  .el-table td, .el-table th.is-leaf,.el-table--border, .el-table--group{
        border-color: black;
    }
    .el-table--border::after, .el-table--group::after, .el-table::before{
        background-color: black;
    }


</style>



