<template>
    <div style="width:105px">
            <div  class="select">
                {{title}}
            </div>

            <el-input type='number' v-show="edit"  @change="inputDataAction" class="inputCss" v-for="item in inputData"  
                      :key="item.id" v-model="item.data" placeholder="请输入"></el-input>

            <div v-show="!edit"  class="inputdiv" v-for="item in inputData"  
                      :key="item.id"  :disabled="true">{{item.data}}</div>
             
    </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        
      }
    },
    props: {
      inputData: Array,
      title: String,
      edit: Boolean
    },
    mounted() {
      
    },
    methods: {
        inputDataAction(){
      // console.log("输入数据 === ",this.inputData)
    },
    }
  }
</script>

<style scoped >
    .select{
    
    font-size: 13px;
    height: 40px;
    line-height: 40px;
    text-align: center;
     border-top: 1px solid;
    border-right: 1px solid;
     border-bottom: 1px solid;
}
.inputdiv{

    
    border-right: 1px solid;
    border-left: 0px solid;
    border-top: 0px solid;
    border-bottom: 1px solid;
    height: 40px;
    line-height: 40px;
    text-align: center;
   
}
.inputCss>>>.el-input__inner{

    border-radius: 0px;
    border-right: 1px solid;
    border-left: 0px solid;
    border-top: 0px solid;
    border-bottom: 1px solid;
    height: 41px;
    text-align: center;
   
}
</style>


