<template>
  <div >
      <div>CustomerData</div>
<el-container style="height: 500px; border: 1px solid #eee">
    
 
  <el-container>
   
    <el-main>
      <el-table 
       :data="tableData"
        ref="multipleTable"
       @selection-change="handleSelectionChange"
        highlight-current-row
        @current-change="handleCurrentChange"
      >
       <el-table-column  type="selection" width="55">
        </el-table-column>
        <el-table-column prop="customerName" label="客户名称" width="140">
        </el-table-column>
        <el-table-column prop="date" label="日期" >
        </el-table-column>
        <el-table-column prop="cement" label="水泥" width="120">
        </el-table-column>
        <el-table-column prop="sand" label="砂子">
        </el-table-column>
        <el-table-column prop="pebble" label="石子">
        </el-table-column>
        <el-table-column prop="flyash" label="粉煤灰">
        </el-table-column>
        <el-table-column prop="mountainFlour" label="石粉">
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</el-container>
  </div>
</template>

<script>
export default {
  name: 'customerData',
  data () {
      
      return {
        multipleSelection: [],
        currentRow:[],
        tableData:[ {
        customerName: '赵宁1',
        date:'2021-1-1',
        cement: '水泥',
        sand: '砂子',
        pebble:'石子',
        flyash:'粉煤灰',
        mountainFlour:'石粉'
      },
      {
        customerName: '赵宁2',
        date:'2021-1-1',
        cement: '水泥',
        sand: '砂子',
        pebble:'石子',
        flyash:'粉煤灰',
        mountainFlour:'石粉'
      },
      ]
      }
  },
   methods: {
     
    }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }
  
  .el-aside {
    color: #333;
  }
</style>



