import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/Home/home'
import customerData from '@/components/customerData/customerData'
import screen from '@/components/experimentalRecord/sand/screen'
import apparent from '@/components/experimentalRecord/sand/apparent'
import Accumulation from '@/components/experimentalRecord/sand/accumulation'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      children: [{
        path: '/',
        component: customerData,
        name: '客户数据',
        hidden: true
      },
      {
        path: '/screen',
        component: screen,
        name: '筛分析实验',
        hidden: true
      },
      {
        path: '/apparent',
        component: apparent,
        name: '表观密度实验',
        hidden: true
      },
      {
        path: '/Accumulation',
        component: Accumulation,
        name: '堆积密度实验',
        hidden: true
      },
    
    ]
    }
    // {
    //   path: '/Home/customerData',
    //   name: 'customerData',
    //   component: customerData
    // }

  ]
})



